.box {
    height: 50px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding: 10px 20px;
    border-radius: 30px;
    align-items: center;
    text-align: center;
}

.box input {
    width: 0;
    outline: none;
    border: none;
    font: 500;
    transition: 0.8s ease-in-out;
    background-color: transparent;
}

.box input.expanded {
    width: 400px;
    height: 30px;
    border: 1px solid black;
    border-radius: 30px;
    padding-left: 20px;
}

.box a .fas {
    background-color: black;
    padding: 10px;
    border-radius: 20px;
    color: rgb(247, 242, 242);
}

@media screen and (max-width: 555px) {
    h1 {
        font-size: 1.5rem;
    }
}