.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}
.hero-mid{
    width: 100%;
    height: 70vh;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-text{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
}
.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    color: white;
    background-color: rgba(0, 0, 0,0.1);
}
.hero-text p{
    font-size: 1.3rem;
    color: rgb(255, 255, 255);
    padding: 0.5rem 0.2rem 0;
}
.hero-text .show{
    position: absolute;
    margin-top: 50px;
    transform: translate(-50%,-50%);
    text-decoration: none;
    background-color: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    color: black;
    letter-spacing: 2px;
}
.hero-text .show:hover{
    background-color: #161515;
    color: #fff;

}
@media screen and (max-width:555px){
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;

    }
    .hero-text .show{
        position: absolute;
        margin-top: 10px;
        transform: translate(-50%,-50%);
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
    }
}
.hero-text .hide{
    display: hide;
}
.hero-text .destin{
    display: hide;
}
.hero-destin{
    height: 75vh;
}
.hero-contact{
    position: relative;
    width: 100%;
    height: 70vh;
}