.Details{
    display: flex;
    align-items: center;
    justify-content:space-around;
    width: 100%;
    margin-top: 50px;
}
.info{
    width: 50%;
}
.Images {
    width: 50%;
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}
.Images img{
    width: 49%;
    height: 300px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 90px -18px rgba(0, 0, 0, 0.19);
}
.Images img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}