.contactForm{
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.contactForm form{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}

.contactForm input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: 0.3rem;
    border: 1px solid #2a2a2a;
}

.contactForm textarea {
    height: 3rem;
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: 0.3 rem;
    border: 1px solid #2a2a2a;
}

@media screen and (max-width: 850px){
    .contactForm{
        margin:4rem 2rem;
    }
    .contactForm form{
        padding-top: 2rem;
        width: 90%;
    }
}